import defaultsDeep from 'lodash/defaultsDeep'
import Builder from './Builder'
import Server from './Server'

export default class Her {

  constructor(options) {
    // 
    this.server = null

    // 
    this.builder = null

    // 
    this.setConfig(options)

    // 
    this.server = new Server(this)

    // 
    this.builder = new Builder(this)
  }

  setConfig(options) {
    this.defaultOptions = defaultsDeep(options, {
      // TODO: 
      // 
      dev: false,
      // 
      env: {},
      // 
      server: {
        host: '0.0.0.0',
        port: '3000',
        middlewares: []
      },
      loading: {
        color: 'black',
        failedColor: 'red',
        height: '2px',
        duration: 5000,
        rtl: false
      },
      entry: '',
      // 
      statics: [
        'static'
      ],
      // loaderoptions
      styleLoader: {
      },
      // 
      assetsPath: '__her__/',
      // 
      assetslog: function (state) { return Promise.resolve() },
      srcDir: '',
      rootDir: ''
    });
  }

  /**
   * 
   * 
   * @memberof Her
   */
  async start(isFirst) {
    await this.server.listen(isFirst)
  }

  /**
   * 
   * 
   * @memberof Her
   */
  async stop() {
    await this.server.stop()
  }

  /**
   * 
   * 
   * @memberof Her
   */
  async destory() {
    await this.server.stop();
  }
}